
*,:after,:before{-webkit-box-sizing:inherit;box-sizing:inherit
}
*>:first-child{margin-top:0
}
*>:last-child{margin-bottom:0
}
html{height:100%;-webkit-box-sizing:border-box;box-sizing:border-box
}
body{min-height:100%;margin:0;position:relative;-webkit-tap-highlight-color:transparent;-webkit-touch-callout:none;-webkit-text-size-adjust:100%;-ms-text-size-adjust:100%;-moz-osx-font-smoothing:grayscale;-webkit-font-smoothing:antialiased;font-family:Roboto,Noto Sans,-apple-system,BlinkMacSystemFont,sans-serif
}
a:not(.md-button){-webkit-transition:.3s ease;transition:.3s ease;-webkit-transition-property:color,background-color,opacity;transition-property:color,background-color,opacity
}
audio,canvas,embed,iframe,img,object,video{max-width:100%;font-style:italic;vertical-align:middle
}
audio:not(.md-image),canvas:not(.md-image),embed:not(.md-image),iframe:not(.md-image),img:not(.md-image),object:not(.md-image),video:not(.md-image){height:auto
}
[tabindex="-1"]:focus{outline:none!important
}
.typo-caption{font-size:12px;font-weight:400;letter-spacing:.02em;line-height:17px
}
.typo-body-1{font-weight:400;line-height:20px
}
.typo-body-1,.typo-body-2{font-size:14px;letter-spacing:.01em
}
.typo-body-2{font-weight:500;line-height:24px
}
