.ripple,.rmh-button:not(.disabled),.rmh-list-item.link,.rmh-select:not(.disabled) .rmh-select-arrow,.rmh-tab-label{position:relative;-webkit-transform:translateZ(0);transform:translateZ(0)
}
.ripple:after,.rmh-button:not(.disabled):after,.rmh-list-item.link:after,.rmh-select:not(.disabled) .rmh-select-arrow:after,.rmh-tab-label:after{content:" ";display:block;position:absolute;width:100%;height:100%;top:0;left:0;pointer-events:none;background-image:radial-gradient(circle,#000 10%,transparent 10.01%);background-repeat:no-repeat;background-position:50%;-webkit-transform:scale(10);transform:scale(10);opacity:0;transition:opacity 1s,-webkit-transform .5s;-webkit-transition:opacity 1s,-webkit-transform .5s;transition:transform .5s,opacity 1s;transition:transform .5s,opacity 1s,-webkit-transform .5s
}
.ripple:active:after,.rmh-button:not(.disabled):active:after,.rmh-list-item.link:active:after,.rmh-select:not(.disabled) .rmh-select-arrow:active:after,.rmh-tab-label:active:after{-webkit-transform:scale(0);transform:scale(0);opacity:.2;-webkit-transition:0s;transition:0s
}
.ripple{overflow:hidden
}
#app {
  font-size: 15px;
}
#app a {
  text-decoration: none;
}
#app .content-container {
  margin: auto;
  max-width: 1000px;
  padding-bottom: 100px;
}
#app p {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  color: #555;
}
#app p .rmh-icon {
  margin-right: 10px;
}
#app .headline {
  padding: 10px;
  padding-left: 10px;
  margin: 20px 0 30px 0;
  border-radius: 5px;
}
#app .warning {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  padding: 10px 20px;
  background: #f7edd5;
  color: #94721f !important;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  border-radius: 5px;
}
#app .warning .rmh-icon {
  font-size: 20px;
  margin-right: 10px;
}
#app .rmh-tabs {
  margin-bottom: 30px;
}
#app .rmh-tabs .rmh-tabs-navigation {
  background: #e5e5e5;
  border-radius: 0 5px 0 0;
}
#app .rmh-tabs .rmh-tab-header {
  background: #eee;
  border-right: 1px solid #ddd;
}
#app .rmh-tabs .rmh-tab-header:first-of-type {
  border-radius: 5px 0 0 0;
}
#app .rmh-tabs .rmh-tab-header.active {
  background: #fff;
}
#app .rmh-tabs .rmh-tabs-body {
  background: #fff;
  border-radius: 0 0 7px 7px;
  border-top: 2px solid #ddd;
  -webkit-box-shadow: 10px 10px 30px -20px #aaa;
          box-shadow: 10px 10px 30px -20px #aaa;
}
.page-components-button .rmh-button:not(.icon-button) .rmh-icon {
  margin-right: 5px;
}
.page-components-button .rmh-icon {
  font-size: 16px;
}
.code-block {
  border-radius: 5px;
  border: 5px dotted #eee;
  overflow: hidden;
}
.code-block pre {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.code-block pre .hljs {
  width: 100%;
  max-height: 220px;
  background: #f5f5f5;
}
.page-components-dialog .rmh-dialog-container {
  max-width: 500px;
}
.page-components-input .rmh-card {
  max-width: 500px;
}
.page-components-textbox .rmh-field {
  max-width: 400px;
}/*

Original highlight.js style (c) Ivan Sagalaev <maniac@softwaremaniacs.org>

*/

.hljs {
  display: block;
  overflow-x: auto;
  padding: 0.5em;
  background: #F0F0F0;
}

/* Base color: saturation 0; */

.hljs,
.hljs-subst {
  color: #444;
}

.hljs-comment {
  color: #888888;
}

.hljs-keyword,
.hljs-attribute,
.hljs-selector-tag,
.hljs-meta-keyword,
.hljs-doctag,
.hljs-name {
  font-weight: bold;
}

/* User color: hue: 0 */

.hljs-type,
.hljs-string,
.hljs-number,
.hljs-selector-id,
.hljs-selector-class,
.hljs-quote,
.hljs-template-tag,
.hljs-deletion {
  color: #880000;
}

.hljs-title,
.hljs-section {
  color: #880000;
  font-weight: bold;
}

.hljs-regexp,
.hljs-symbol,
.hljs-variable,
.hljs-template-variable,
.hljs-link,
.hljs-selector-attr,
.hljs-selector-pseudo {
  color: #BC6060;
}

/* Language color: hue: 90; */

.hljs-literal {
  color: #78A960;
}

.hljs-built_in,
.hljs-bullet,
.hljs-code,
.hljs-addition {
  color: #397300;
}

/* Meta color: hue: 200 */

.hljs-meta {
  color: #1f7199;
}

.hljs-meta-string {
  color: #4d99bf;
}

/* Misc effects */

.hljs-emphasis {
  font-style: italic;
}

.hljs-strong {
  font-weight: bold;
}
